html {
  background-color: white;
  font-family: "Noto Sans JP", "Helvetica Neue", "Helvetica", "Hiragino Sans", "Hiragino Kaku Gothic ProN", "Arial", "Yu Gothic", "Meiryo", sans-serif;
  font-size: 16px;
}

h2 {
  font-size: 24px;
}

.App {
  width: 80%;
  margin: auto;
}

.App-header {
  display: inline-flex;
  width: 100%;
  box-sizing: border-box;

  margin: 10px auto;
  padding: 15px;

  justify-content: left;
  border-bottom: 1px solid #ccc;
}

.App-header img {
  height: 50px;
}

.title {
  font-size: 32px;
  font-weight: bold;
  margin-left: 15px;
}

.App-footer {
  padding: 20px;
  text-align: center;
  border-top: 1px solid #ccc;
  margin: 30px;
  padding: 20px;
}

.charaList {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.charaList .charaLink {
  height: 60px;
  width: 60px;

  display: inline-flex;
  justify-content: center;
  align-items: center;

  margin: 5px;
  border: 2px solid #c0c0c0;
  font-size: 25px;
}

.charaList .charaLink:hover {
  background-color: #d5f0eb;
}

.charaList > a {
  color: initial;
  text-decoration: none;
}

.pageWrapper {
  display: flex;
  flex-flow: row;
}

.info {
  display: flex;
  flex-flow: column;
  width: 25%;
  margin: 0 20px;
}

.info > .charaFrame {
  width: 100%;
  height: 150px;
  display: inline-flex;
  justify-content: center;
  align-items: center;
}

.info > .charaFrame > .mainChara {
  font-size: 100px;
}

.phun {
  font-family: "PhunWrite", "Times New Roman", "YuMincho", "Hiragino Mincho ProN", "Yu Mincho", "MS PMincho", serif;
}

.dataTable {
  border-collapse: collapse;
  border-spacing: 0;
}

.dataTable * {
  border: 1px solid #a0a0a0;
  padding: 5px;
}

.dataTable th {
  background-color: #f5f5f5;
  width: 30%;
}

.langs {
  flex-grow: 1;
}

.langs > .langData > h2 {
  border-bottom: 3px solid #c0c0c0;
  font-size: 28px;
}

.langs > .langData > .dataEntry > h3 {
  background-color: #dcf2ee;
  border-left: 3px solid #014f41;
  padding-left: 5px;
  padding: 3px 0 3px 0.3em;
  margin-top: 20px;
  margin-bottom: 10px;
  font-size: 20px;
}

.langs > .langData > .dataEntry > p {
  margin: 0;
}

.phun404 {
  font-size: 24px;
}

@media screen and (max-width: 750px) {
  .App {
    width: 90%;
  }
  .pageWrapper {
    flex-flow: column;
  }
  .info {
    width: 100%;
    margin: 0;
  }
}

@font-face {
  font-family: "PhunWrite";
  src: url(https://kaeru2193.github.io/Phun-Resources/font/PhunWrite-Regular-latest.woff) format("woff");
}